<script>
  import { afterUpdate, tick } from 'svelte';
  import {
    sendMessage,
    loadMessagesWS,
    messages,
    status,
    myId,
    myName,
    conversations,
    conversationId
  } from '$lib/websocket-client.js';

  import { isDifferentDay } from '$lib/utils/datetime';
  import TimelineDivider from '@components/messenger/TimelineDivider.svelte';
  import Message from '@components/messenger/Message.svelte';
  import Conversion from '@components/messenger/Conversation.svelte';

  let input = '';
  let selected = null;

  let chatBox;
  let isLoadingMore = false;
  let isPrepend = false;
  let needScrollBottom = false;
  let prevHeight = 0;

  // Bin quan trng: lu li tin u tin  dng  load c
  let lastLoadedFirstMsgId = null; //  y l "ci kha" ngn duplicate

  // Thay hon ton hm handleScroll bng ci ny:
  async function handleScroll() {
    const threshold = 100;
    if (!chatBox) return;
    if (isLoadingMore) return;
    if (chatBox.scrollTop > threshold) return; // cch u 100px mi cho load
    if ($messages.length === 0) return;

    if (chatBox.scrollTop === 0) {
      chatBox.scrollTop = threshold + 1;
    }

    const currentFirstMsg = $messages[0];

    // Nu  tng load vi ng tin u ny ri  b qua lun
    if (lastLoadedFirstMsgId === currentFirstMsg.id) {
      //console.log(' load tin c vi firstMsg ny ri, b qua');
      return;
    }

    // Bt u load  nh du ang load + lu li id ny
    isLoadingMore = true;
    isPrepend = true;
    prevHeight = chatBox.scrollHeight;
    lastLoadedFirstMsgId = currentFirstMsg.id; //  kha li, ln sau khng load na

    //console.log('ang load tin c hn tin:', currentFirstMsg.id);

    try {
      await loadMessagesWS($conversationId, {
        firstMsg: currentFirstMsg
      });
    } catch (err) {
      console.error('Load older messages li:', err);
      // Nu li th cho php th li ln sau
      lastLoadedFirstMsgId = null;
    } finally {
      isLoadingMore = false;
    }
  }

  // Gi nguyn afterUpdate  fix v tr scroll
  afterUpdate(() => {
    if (!chatBox) return;

    if (needScrollBottom && $messages.length) {
      chatBox.scrollTop = chatBox.scrollHeight;
      needScrollBottom = false;
      return;
    }

    if (isPrepend) {
      const delta = chatBox.scrollHeight - prevHeight;
      chatBox.scrollTop += delta;
      isPrepend = false;
    }
  });

  // Reset li khi i cuc tr chuyn
  async function selectConversation(conv) {
    if (!conv?.conversationId) return;

    selected = conv.conversationId;
    conversationId.set(selected);

    messages.set([]);
    lastLoadedFirstMsgId = null; //  QUAN TRNG: reset khi i phng
    needScrollBottom = true;

    await loadMessagesWS($conversationId);
  }

  function send() {
    console.log('send:', input.trim(), selected);
    if (!input.trim() || !selected) return;

    sendMessage({
      type: 'chat',
      conversationId: selected,
      from: myId,
      message: input.trim()
    });

    input = '';

    setTimeout(() => {
      if (chatBox) {
        chatBox.scrollTop = chatBox.scrollHeight;
      }
    }, 300);
  }
</script>

<!-- =========================
     LAYOUT
========================= -->
<main class="flex h-screen bg-gray-100 text-gray-800">
  <!-- SIDEBAR -->
  <aside class="w-56 border-r p-3 bg-white flex flex-col gap-2">
    <p class="text-sm font-medium flex items-center gap-2 mb-2">
      <span class="font-semibold text-blue-600 truncate">
        {$myName}
      </span>
      <span
        class="text-[10px] px-2 rounded-full
        {$status.includes('Connected') ? 'bg-green-100 text-green-600' : 'bg-red-100 text-red-600'}">
        {$status.replace(/\s.*$/, '')}
      </span>
    </p>

    {#each $conversations as conv}
      <Conversion conversation={conv} selectedId={selected} onSelect={selectConversation} />
    {/each}
  </aside>

  <!-- CHAT -->
  <div class="flex-1 flex flex-col">
    <!-- MESSAGES -->
    <div bind:this={chatBox} onscroll={handleScroll} class="flex-1 overflow-y-auto p-3 border-b">
      {#if !selected}
        <p class="text-center text-gray-400 mt-5">Select a user to start chatting</p>
      {:else}
        {#if isLoadingMore}
          <p class="text-center text-xs text-gray-400 mb-2">Loading more</p>
        {/if}

        {#each $messages as msg, i (msg.id)}
          {#if i === 0 || isDifferentDay(msg.created_at, $messages[i - 1]?.created_at)}
            <TimelineDivider time={msg.created_at} locale="vi" />
          {/if}

          <Message {msg} myId={$myId} />
        {/each}
      {/if}
    </div>

    <!-- INPUT -->
    <form
      onsubmit={(e) => {
        e.preventDefault();
        send();
      }}
      class="p-3 flex gap-2 bg-white">
      <input
        bind:value={input}
        class="flex-1 border rounded px-3 py-2 focus:ring-2 focus:ring-blue-400"
        placeholder="Type message..." />
      <button type="submit" class="px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600">Send</button>
    </form>
  </div>
</main>
